.home{width: 100%; height: 100vh; background-color: aliceblue; display: flex; position: relative; overflow: hidden; overflow-y: scroll; padding: 60px 0 0;}
.home::-webkit-scrollbar{display: none;}

.front{width: 100vw; height: 100vh; position: fixed; background-color: black; z-index: 101; top: 0;}
.front-title{font-size: 5em; color: white; position: absolute; bottom: 60%; left: 50%; transform: translateX(-50%); font-weight: 900; animation-name: movein; animation-duration: 2s;} 

header{position: fixed;top: 0; height: 60px; background-color:black; width: 100%; z-index: 100;}
.header-wrap, .footer-wrap {max-width: 1200px; margin: 0 auto; position: relative; height: 100%;}
header a, footer a{position: absolute; left: 20px; top: 50%; transform: translateY(-50%); color: white; font-size: 2rem; font-weight: 900;}

footer{position: fixed; z-index: 100; width: 100%; height: 60px; bottom: 0; background-color: black;}
footer a{width: 100%;text-align: center;}

.center{top: 50%; left: 50%; transform: translate(-50%, -50%);}
.footer-wrap > a > span{margin: 0 10px; color: #FFD800; transition: all .35s ease-out; animation-duration: 5s; animation-iteration-count: infinite; animation-name: fleshlight;}

@keyframes fleshlight{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}  

@keyframes movein{
    from{
        bottom: 58%;
        opacity: 0;
    }

    to{
        bottom: 60%;
        opacity: 1;
    }
}

.container{margin: 0 20px; width: 100%; display: flex; flex-direction: row;}

@media all and (min-width:280px){
    footer a, header a{font-size: 2.1rem;}
}
@media all and (min-width:320px){
    footer a, header a{font-size: 2.3rem;}
}
@media all and (min-width:360px){
    footer a, header a{font-size: 2.8rem;}
}
@media all and (min-width:400px){
    footer a, header a{font-size: 3rem;}
}