.answers{padding: 60px 0 80px;}

.interviews{display: flex; flex-direction: column; margin: 20px 20px;  background-color: rgba(0, 0, 0, .1); max-width: 500px;}
.i-q{background-color: black; padding: 10px; color: white; font-size: 1.4rem; border-top-left-radius: 5px; border-top-right-radius: 5px;}

.interview{display: flex;flex-flow: row wrap; margin-top: 20px; padding: 0 20px;}

.i-wrap{margin: 0 auto 10px; text-align: center;}

.i-ee{font-size: 1.4rem; font-weight: 600;margin-top: 5px;}
.i{font-size: 1.3rem; text-indent: 5px; line-height: 1.6;}

.i-back{background-color: black; padding: 20px; color: white; font-size: 1.4rem; border-bottom-left-radius: 5px; border-bottom-right-radius: 5px;margin-top: 20px; position: relative}

.prev, .next, .back{position: absolute; top: 50%; transform: translateY(-50%); }
.prev{left: 20px;}
.next{right: 20px;} 

.back{left: 50%; transform: translate(-50%, -50%);}

.i-title{font-size: 2rem; font-weight: 800; width: 100%; text-align: center; margin-bottom: 10px;}

.interview ul{padding: 0 15px;}
.interview li{font-size: 1.4rem; }

.i-img{margin: 0 0 20px;font-size: 1.4em;}

@media all and (min-width: 500px){
    .interviews{max-width: 500px; margin: 20px auto 20px;}
}